export class InputText {
    constructor(type, required) {
        this.type = type;
        this.required = required;
    }
}
export class Select {
    constructor(multiple, required, options) {
        this.multiple = multiple;
        this.required = required;
        this.options = options;
    }
}
export class Textarea {
    constructor(required) {
        this.required = required;
    }
}
